import logo from './logo.svg';
import { useEffect, useState } from 'react';
import './App.css';
import { keyboard } from '@testing-library/user-event/dist/keyboard';


function App(){

  const [activeDisplay, setDisplay] = useState('');

  const padBank = [
    {
      keyCode:81,
      display: "Heater-1",
      text:"Q",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3",
    },
    {
      keyCode:87,
      display: "Heater-2",
      text:"W",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3",
    },
    {
      keyCode:69,
      display: "Heater-3",
      text:"E",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3",
    },
    {
      keyCode:65,
      display: "Heater-4",
      text:"A",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3",
    },
    {
      keyCode:83,
      display: "Clap",
      text:"S",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3",
    },
    {
      keyCode:68,
      display: "Open-HH",
      text:"D",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3",
    },
    {
      keyCode:90,
      display: "Kick-n'-Hat",
      text:"Z",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3",
    },
    {
      keyCode:88,
      display: "Kick",
      text:"X",
      src: "https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3",
    },
    {
      keyCode:67,
      display: "Closed-HH",
      text:"C",
      src: "https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3",
    },
  ]

  useEffect(() => {
    document.addEventListener('keydown',(event)=>{playAudio(event.key.toUpperCase())})
  })

  function playAudio(selector){
    const audio=document.getElementById(selector);
    audio.play();
    for (let i=0;i<padBank.length;i++) {
      if (padBank[i].text === selector) {
         setDisplay(padBank[i].display);
      }
    }
  };
 
  return (
  
    <div className="App">
      <div class="inner-container" id="drum-machine">
        <div class="pad-bank">
          {padBank.map((drumPad) => <div class="drum-pad" id={drumPad.display} onClick={() => {playAudio(drumPad.text)}}><audio class="clip" id={drumPad.text} src={drumPad.src}></audio>{drumPad.text}</div>)}
        </div>
        <div class="controls-container">
          <div class="control"></div>
          <p id="display">{activeDisplay}</p>
          <div class="volume-slider"></div>
          <div class="control"></div>
        </div>
      </div>

    </div>
  );

 
}

export default App;
